
.parralax-example {
    font-family: 'Bebas Neue', cursive;
    font-size: 72px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    border: 1px solid white;

    &-mountain {
        width: 100vw;
        height: 100vh;
        background-size: cover;
       background-position: 50%;
       align-items: center;
       position: relative;
       &-bg {
           position: absolute;
           width: 100%;
           height: 100%;
       }
       &-content-one {
           height: 100vh;
           width: 100vw;
           font-size: 50px;
           color: white;
           display: flex;
           justify-content:center;
           align-items: center;
           &-text {
               font-size: 50px;
               color: white;
           }
       }
       &-content-two {
        height: 100vh;
        width: 100vw;
        font-size: 50px;
        color: white;
        display: flex;
        justify-content:center;
        align-items: center;
        &-text {
            font-size: 50px;
            color: white;
        }
    }
    }

    &-two {
        font-family: 'Bebas Neue', cursive;
        font-size: 72px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: center;
        align-content: center;
        align-items: center;
        border: 1px solid white;  
        &-left {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            h2 {
                margin: 0px 100px 0 0;
                padding: 0;
            }
        }
        &-right {
            display: inline-flex;
            justify-content: center;
            align-items: center;
            h2 {
                margin: 0 0 0 200px;
                padding: 0;
            }
        }
    }
    &-left {
        flex: 1 1 400px;
        background-color: black;
        color: white;
        display: flex;
        justify-content: center;
        height: 60vh;
        overflow: hidden;
        &-text {
            margin-top: 200px;
        }
    }
    &-right {
        flex: 1 1 400px;
        background-color: red;
        display: flex;
        justify-content: center;
        top: -1rem;
        right: 0;
        bottom: -1rem;
        left: 0;
        height: 60vh;
        overflow: hidden;
        &-img {
            height: 100vh;
            flex: 1 1 400px;
            background-color: red;
            display: flex;
            justify-content: center;
            top: -1rem;
            right: 0;
            bottom: -1rem;
            left: 0;
            background-size: cover;
            background-position: 50%;
        }
    }
}


#shape-dash {
    fill: blue;
    /* stroke: wheat; */
    width: 163px;
    height: 41px;
    margin-top: 100px;
}

.plus-svg {
    fill: blue;
    /* stroke: wheat; */
    width: 100px;
    height: 100px;
    margin-top: 100px;  
    margin-left: -25px;
    transform: rotate(45deg);
}


// three component parallax example

.three-component {
    padding-top: 1000px;
    padding-bottom: 1000px;
    background-color: #353530;
    overflow: hidden;
    width: 100%;
    .box {
        width: 100px;
        height: 100px;
        color: black;
        background-color: yellow;
        margin: 200px 0 200px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        &-2 {
            background-color: red;
        }

        &-3 {
            background-color: blue;
            margin-bottom: 0;
        }
    }
}