
body {
    margin: 0;
    padding: 0;
  }
  
  .animation-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
    top: 0;
    background-color: black;
  }
  
  #animteSection {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
  
    .phone-wrapper {
      width: 70%;
      max-height: 90vh;
      padding-top: 5vh;
      background-color: rgb(0, 174, 189);
      padding-bottom: 5%;
      overflow: hidden;
      z-index: 2;
      margin: auto;
      position: relative;
      text-emphasis: center;
      .phone-img {
        max-height: 90vh;
        min-width: 300px;
        margin: auto;
        text-align:center;
      }
      .phone-tooltip {
        height: 100px;
        position: absolute;
        top: 135px;
        /* left: 0; */
        right: calc(150px + 13%);
      }
      .phone-hand {
        height: 275px;
        position: absolute;
        top: 281px;
        /* left: 0; */
        right: -247px;
        right: calc(-247px + 15%);
  
      }
  
      .phone-hand-text {
        height: 100px;
        position: absolute;
        top: 335px;
        /* left: 0; */
        right: 223px;
        right: calc(223px + 15%);
  
      }
      .phone-crane {
        height: 300px;
        position: absolute;
        top: -70px;
        left: 400px;
      }
      .phone-crane-icon {
        position: absolute;
        height: 70px;
        top: 214px;
        left: 419px;
      }
      .phone-crane-two {
        position: absolute;
        height: 236px;
        top: 445px;
        left: 263px;
      }
  
      .phone-cloud-one {
        position: absolute;
        width: 400px;
        /* height: 300px; */
        top: -90px;
        left: 0;
      }
  
      .phone-cloud-two {
        position: absolute;
        width: 800px;
        /* height: 300px; */
        top: -70px;
        left: 0;
        z-index: 2;
      }
  
      .phone-cloud-three {
        position: absolute;
        width: 1000px;
        /* height: 300px; */
        top: -40px;
        left: 0;
        z-index: 2;
      }
  
      .phone-cloud-four {
        position: absolute;
        width: 1200px;
        /* height: 300px; */
        top: -20px;
        left: 0;
        z-index: 2;
      }
  
      .phone-cloud-five {
        position: absolute;
        width: 1400px;
        /* height: 300px; */
        top: 0px;
        left: 0;
        z-index: 2;
      }
  
      .phone-cloud-six {
        position: absolute;
        width: 1600px;
        /* height: 300px; */
        top: 20px;
        left: 0;
        z-index: 2;
      }
    }
  }